@media (max-width: 1100px) {
	header {
		padding-top: 2rem;
	}

	/* General */

	.about,
	.contact {
		width: 85%;
	}

	.blog,
	.projects,
	.contact {
		margin-top: 0;
		padding-top: 14.03rem;
	}

	header {
		background-color: rgba(40, 42, 54, 0.9);
		backdrop-filter: blur(20px) saturate(180%);
		border-radius: 0 0 10px 10px;
		box-shadow: 0 0 5px 4px rgba(50, 52, 75, 0.6);
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 998;
	}

	.header-title {
		font-size: 3.5rem;
	}

	.about-header-title {
		font-size: 3.5rem;
	}

	.center-header {
		margin-right: 5px;
	}

	.section-subheader {
		margin-top: 5rem;
	}

	.marg-bottom {
		margin-bottom: 3rem;
	}

	/* Nav */

	.nav-desktop {
		display: none;
	}

	.nav-wrapper {
		margin-top: -20px;
		padding-right: 10px;
	}

	.about-nav-wrapper {
		margin-top: 0;
	}

	.nav-wrapper .menu {
		border: none;
		cursor: url('../assets/images/icons/cursor-hand-white.png'), auto;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0;
		padding-left: 50px;
		z-index: 1000;
		position: relative;
	}

	.nav-wrapper .navbar {
		background: var(--dracula-background);
		box-shadow: 0 0 5px 2px rgba(189, 147, 249, 0.4);
		color: var(--dracula-foreground);
		display: initial;
		height: 385%;
		left: 0;
		overflow: hidden;
		position: absolute;
		top: 0;
		transform: translateY(-110%);
		-webkit-transform: translateY(-110%);
		transition: transform 0.2s ease;
		width: 100%;
		z-index: 999;
	}

	.nav-wrapper .navbar.showNav {
		transform: translateY(0);
		-webkit-transform: translateY(0);
		transition: transform 0.2s ease;
	}

	.nav-wrapper .navbar ul {
		align-items: center;
		display: flex;
		flex-flow: column nowrap;
		height: 100%;
		justify-content: space-evenly;
		list-style: none;
		width: 100%;
	}

	.menu-link a {
		color: var(--dracula-foreground);
		font-size: 1.4rem;
		padding-bottom: 8px;
		text-decoration: none;
		text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4), 0 8px 13px rgba(0, 0, 0, 0.1),
			0 18px 23px rgba(0, 0, 0, 0.1);
		text-transform: uppercase;
		background-repeat: repeat-x;
		background-position: left 0% bottom -5%;
		background-image: linear-gradient(
			90deg,
			rgba(139, 233, 253, 1) 0%,
			rgba(189, 147, 249, 1) 50%,
			rgb(255, 121, 198, 1) 100%
		);
		background-size: 100% 12%;
		transition: ease color 0.2s;
		letter-spacing: 5px;
	}

	.menu-link a:hover {
		color: var(--dracula-purple);
		transition: ease color 0.2s;
	}

	.s {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.s img {
		position: relative;
		top: 25px;
		text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4), 0 8px 13px rgba(0, 0, 0, 0.1),
			0 18px 23px rgba(0, 0, 0, 0.1);
	}

	.mode-toggle {
		position: initial;
		margin-right: 0;
		padding: 0;
	}

	.checkbox-label {
		background-color: var(--dracula-selection);
	}

	/* About */

	.about {
		scroll-margin-top: 150px;
	}

	.terminal-container {
		margin-top: 0;
		padding-top: 14rem;
	}

	.profile-image {
		max-width: 35%;
	}

	.profile-div {
		margin-bottom: 0;
	}

	/* Blog */

	.blog-container {
		padding-top: 1.5rem;
		margin-bottom: 2rem;
	}

	.blog .subheader {
		width: 75%;
	}

	.dev-article {
		width: 75%;
	}

	.dev-article:first-of-type {
		margin-top: 0;
	}

	.dev-article:not(:last-of-type) {
		margin-bottom: 2rem;
	}

	/* Projects */

	.modal {
		z-index: 1001;
	}

	.modal-content {
		padding: 1rem;
		align-items: center;
		margin-top: auto;
		margin-bottom: auto;
		height: auto;
		width: 90%;
	}

	.close-button {
		width: 2rem;
		line-height: 2rem;
		font-size: 1.5rem;
	}

	.modal-header {
		font-size: 1.2rem;
	}

	.projects .subheader {
		width: 82%;
		padding: 25px 15px 25px 15px;
	}

	.projects-container {
		width: 85%;
		margin: 0 auto;
	}

	.projects-p,
	.blog-p {
		text-align: center;
	}

	.project-name0,
	.project-name1,
	.project-name2,
	.project-name3,
	.project-name4,
	.project-name5 {
		background-color: var(--dracula-foreground);
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
		box-shadow: 0 0 2px 1px var(--dracula-foreground);
		color: black;
		font-size: 1.3rem;
		font-weight: 500;
		padding: 1rem 0;
		transition: none;
		width: 100%;
		margin-top: 5px;
		text-shadow: none;
	}

	.project-image {
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
		border-top-left-radius: 5px;
		border-top-right-radius: 5px;
		box-shadow: 0 0 2px 1px var(--dracula-foreground);
		max-width: 100%;
		opacity: 1;
		transition: none;
	}

	.project-tile:hover .project-image {
		opacity: 1;
		box-shadow: 0 0 2px 1px var(--dracula-foreground);
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 0;
		transition: none;
	}

	.project-tile:hover .project-name0,
	.project-tile:hover .project-name1,
	.project-tile:hover .project-name2,
	.project-tile:hover .project-name3,
	.project-tile:hover .project-name4,
	.project-tile:hover .project-name5 {
		text-shadow: none;
		color: black;
		opacity: 1;
		transform: none;
		-webkit-transform: none;
		transition: none;
	}

	.shrink {
		transition: none;
	}

	.shrink:hover {
		-webkit-transform: none;
		-ms-transform: none;
		transform: none;
		transition: none;
	}

	.projects-cell {
		width: 100%;
		padding: 2rem 1rem;
	}

	.projects-cell:first-of-type {
		padding-top: 1.5rem;
	}

	/* Contact */

	.form-container {
		padding-top: 1.5rem;
	}

	.contact-p {
		text-align: center;
	}

	.form-submit {
		opacity: 1;
	}

	@media (max-height: 850px) {
		.terminal-container {
			padding-top: 12.1rem;
			margin-top: 0;
		}

		.arrows {
			bottom: 20px;
		}
	}

	@media (max-height: 760px) {
		.terminal-container {
			padding-top: 11.1rem;
